import { Box, Card, CardBody, CardFooter, Grommet, Heading, TextArea } from "grommet";
import React from "react";
import { Component } from "react";
import { RouteComponentProps } from "react-router-dom";
import { menuConfig } from "../../../../shared/constants/menuItems";
import MenuCompnent from "../../menu/Menu";
import { MessageComponent } from "./MessageComponent";

const theme = {
  global: {
    font: {
      family: 'Prosto One',
    },
    focus: {
      border: {
        color: "#E9FFE1"
      }
    },
    control: {
      border: {
        radius: "20px"
      }
    }
  },
  card: {
    container: {
      background: '#1a3a39',
      elevation: 'none',
    },
    footer: {
      pad: { horizontal: 'medium', vertical: 'small' },
    },
  },
  heading: {
    color: '#ADFF0037',
    level: {
      2: {
        medium: {
          size: "30px",
          height: "40px",
        }
      }
    }
  },
};

class MessagePage extends Component<RouteComponentProps>  {
  state = {};

  render(): JSX.Element {
    document.body.className = "";

    return (<div>
      <MenuCompnent items={menuConfig} />
      <Grommet theme={theme}>
        <Box
          margin="xlarge"
          pad="medium"
          alignSelf='center'
          alignContent="center"
          align="center"
        >
          <Card elevation="large" width="large">
            <CardBody height="small">
              <Heading level="2" alignSelf="center">
                Сообщения темы
              </Heading>
              <MessageComponent
                id="1"
                title="Просто тест"
                createdAt="2021-05-21T17:59:28.384Z"
                authorUserName="Это автор"
              ></MessageComponent>
              <MessageComponent
                id="2"
                title="Это ещё один текст чтобы проверить всё"
                createdAt="2021-05-21T17:59:28.384Z"
                authorUserName="Второй автор"
              ></MessageComponent>
            </CardBody>
            <CardFooter>
              <TextArea placeholder="Новое сообщение">
              </TextArea>
            </CardFooter>
          </Card>
        </Box>
      </Grommet>
    </div>)
  }
}

export default MessagePage;